<template>
  <div id="detail">
    <detail-nav-bar ref="detailnav" class="detail-nav" @titleClick="titleClick"></detail-nav-bar>
    <!-- 必须有固定高度 -->
    <scroll class="content" @scroll="contentScroll" :probe-type="3" ref="scroll">
      <detail-swiper :topImages="topImages"></detail-swiper>
      <detail-base-info :goods="goods"></detail-base-info>
      <detail-shop-info :shop="shop"></detail-shop-info>
      <detail-goods-info :imagesInfo="detailInfo" @imgLoad="imageLoad"></detail-goods-info>
      <detail-param-info ref="params" :param-info="paramInfo"></detail-param-info>
      <DetailCommentInfo ref="comment" :commentInfo="commentInfo"></DetailCommentInfo>
      <good-list ref="recommend" :goods="recommend" @recommendLoad="recommendLoad"></good-list>
    </scroll>
    <back-top @click.native="backClick" v-show="isShowBackTop"></back-top>
    <detail-bottom-bar @addCart="addToCart"></detail-bottom-bar>
  </div>
</template>

<script>
import DetailNavBar from "./childComps/DetailNavBar";
import DetailSwiper from "./childComps/DetailSwiper";
import DetailBaseInfo from "./childComps/DetailBaseInfo";
import DetailShopInfo from "./childComps/DetailShopInfo";
import DetailGoodsInfo from "./childComps/DetailGoodsInfo";
import DetailParamInfo from "./childComps/DetailParamInfo";
import DetailCommentInfo from "./childComps/DetailCommentInfo";
import DetailBottomBar from "./childComps/DetailBottomBar";

import Scroll from "common/scroll/Scroll";
import GoodList from "components/content/goods/GoodList";
import BackTop from "components/content/backTop/BackTop";

import { debounce } from "@/common/utils";
import { itemListenerMixin } from "@/common/mixin";
import {
  getDetail,
  getRecommend,
  Goods,
  Shop,
  GoodsParams
} from "network/detail";
import {mapActions} from 'vuex'


export default {
  name: "Detail",
  components: {
    DetailNavBar,
    DetailSwiper,
    DetailBaseInfo,
    DetailShopInfo,
    Scroll,
    DetailGoodsInfo,
    DetailParamInfo,
    DetailCommentInfo,
    GoodList,
    BackTop,
    DetailBottomBar
  },
  data() {
    return {
      iid: null,
      topImages: [],
      goods: {},
      shop: {},
      detailInfo: {},
      paramInfo: {},
      commentInfo: {},
      isShowBackTop: false,
      recommend: [],
      themeTopYs: [],
      getThemeTopY: null,
      currentIndex: 0
    };
  },
  mixins: [itemListenerMixin],
  created() {
    //   1.保存传入的iid
    this.iid = this.$route.params.iid;
    //   2.根据iid请求详情数据
    getDetail(this.iid).then(res => {
      const data = res.result;
      // console.log(data);
      //  1.获取顶部的轮播数据
      this.topImages = res.result.itemInfo.topImages;
      // console.log(res)
      // 2.获取商品信息
      this.goods = new Goods(
        data.itemInfo,
        data.columns,
        data.shopInfo.services
      );
      //3.创建店铺信息的对象
      this.shop = new Shop(data.shopInfo);

      // 4.保存商品的详细数据
      this.detailInfo = data.detailInfo;
      // 5获取参数信息
      this.paramInfo = new GoodsParams(
        data.itemParams.info,
        data.itemParams.rule
      );

      //6获取评论信息
      if (data.rate.cRate !== 0) {
        this.commentInfo = data.rate.list[0];
      }

      //等子组件渲染完会调用这个组件
      this.$nextTick(() => {
        //根据最新的数据，对应的DOM已经被渲染出来
        // 但是图片已然是没有加载完（目前获取到的offsetTop不包含其中的组件）
        //offsetTop值不对的时候，都是因为图片的问题
        // this.themeTopYs.push(0);
        // this.themeTopYs.push(this.$refs.params.$el.offsetTop);
        // this.themeTopYs.push(this.$refs.comment.$el.offsetTop);
        // this.themeTopYs.push(this.$refs.recommend.$el.offsetTop);
      });
    });

    // 3.请求推荐数据
    getRecommend().then(res => {
      this.recommend = res.data.list;
    });
    this.getThemeTopY = debounce(() => {
      this.themeTopYs = [];
      this.themeTopYs.push(0);
      this.themeTopYs.push(this.$refs.params.$el.offsetTop);
      this.themeTopYs.push(this.$refs.comment.$el.offsetTop);
      this.themeTopYs.push(this.$refs.recommend.$el.offsetTop);
      this.themeTopYs.push(Number.MAX_VALUE);
    }, 100);
  },

  destoryed() {
    this.$bus.$off("itemImageLoad", this.itemImgListener);
  },
  methods: {
    ...mapActions(['addCart']),
    titleClick(index) {
      this.$refs.scroll.scrollTo(0, -this.themeTopYs[index], 100);
    },
    imageLoad() {
      this.$refs.scroll.refresh();
      this.getThemeTopY();
    },
    recommendLoad() {
      this.$refs.scroll.refresh();
    },
    backClick() {
      this.$refs.scroll.scrollTo(0, 0);
    },
    contentScroll(position) {
      const positionY = -position.y;
      // console.log(position);
      // 1.判断BackTop是否显示
      this.isShowBackTop = -position.y > 1000;
      // 2.决定tabControl是否吸顶(position:fixed)
      this.isTabFixed = -position.y > this.tabOffsetTop;
      //positionY和主题中值进行对比
      // [0,7938,9120,9452]
      // positionY在0和7938之间 index为=0
      // positionY在7938和9120之间 index为=1
      // positionY在9128和9452之间 index为=1
      // positionY在超过9452index为=3

      // 第一种判断
      // for (let i = 0; i <= this.themeTopYs.length; i++) {
      //   parseInt(i);
      //   if (
      //     this.currentIndex != i &&
      //     ((i < this.themeTopYs.length - 1 &&
      //       positionY > this.themeTopYs[i] &&
      //       positionY < this.themeTopYs[i + 1]) ||
      //       (i === this.themeTopYs.length - 1 &&
      //         positionY > this.themeTopYs[i]))
      //   ) {
      //     this.currentIndex = i;
      //     this.$refs.detailnav.currentIndex = this.currentIndex;
      //   }
      // }

      // 第二种判断
      for (let i = 0; i <= this.themeTopYs.length - 1; i++) {
        if (
          this.currentIndex != i &&
          positionY > this.themeTopYs[i] &&
          positionY < this.themeTopYs[i + 1]
        ) {
          this.currentIndex = i;
          this.$refs.detailnav.currentIndex = this.currentIndex;
        }
      }
    },
    addToCart() {
      //获取购物车展示的信息
      const product={}
      product.image=this.topImages[0]
      product.title=this.goods.title;
      product.desc=this.goods.desc;
      product.price=this.goods.realPrice;
      product.iid=this.iid;

      //将商品添加到购物车里（1.promise 2.mapActions）
      // this.$store.commit('addCart',product)    
      // this.$store.dispatch('addCart',product).then(res=>{
      //   console.log(res);      
      // })
      // 调用了 mapActions
      this.addCart(product).then(res=>{
        this.$toast.show(res,1500)
        
      })
    }
  }
};
</script>

<style scoped>
#detail {
  position: relative;
  z-index: 1;
  background-color: #fff;
  height: 100vh;
}
.detail-nav {
  position: relative;
  z-index: 9;
  background-color: #fff;
}
.content {
  position: absolute;
  top: 44px;
  bottom: 58px;
}
</style> 